import {Link} from "react-router-dom";
// import {MQTTSub} from "../../js/mqtt";
import {useEffect, useState} from "react";
import mqtt from "mqtt";
import axios from "axios";

export default function List(){
    // MQTT 
    const URL = "nextit.or.kr:21883"; // 21883  protocol   error 
    const client = mqtt.connect(URL,{
        connectTimeout: 5000,
        hostname:"nextit.or.kr",
        port:29001,
    });

    // MQTT  subscribe  message( )   data 
    const [message, setMessage] = useState(null);
    const [num, setNum] = useState(0); // MQTT   
    useEffect(() => {
        //  null         
        if(message){
            console.log("  : ");
            console.log(message)
            console.log(num);
        }
    }, [message]);

    // MQTT connect   event -> subscribe, publish  
    client.on("connect", () => {
        //  publish    subscribe  
        client.subscribe("/IoT/Sensor/Ditto", (err) => {
            if (!err) {
                // React  publish   message
                client.publish("/React/Ditto", "React   message");
            }
        });
    });

    // MQTT broker  message     event
    // setTimeout       broker   message    
    client.on("message", (topic, message) => {
        // message is Buffer
        const parser = JSON.parse(message.toString());
        setMessage(parser);     // MQTT   data  useState  update
        setNum(num+1);   // MQTT subscribe  
        client.end();
    });

    // DB   SEAT Table
    const [seatList, setSeatList] = useState([]);
    const getSeatData = async () => {
        const res = await axios.get("/seat/list");
        setSeatList(res.data);
        // console.log(" : ", res.data);
    };

    //     DB  SEAT    
    useEffect(() => {
        getSeatData();
    }, []);

    //       
    const [seatClick, setSeatClick] = useState(false);
    const goChangeSeat = () => {
        setSeatClick(!seatClick);
    }

    //   
    // 1. seatNo      (,     )
    // 2.   data    seatNo 7    
    // 3. status  Y, N     none( ), used( )   

    //   
    //  <div>  className : static
    //  <div>  className : z-10 absolute transform -translate-x-64 -translate-y-24
    //  grid, grid-cols-2  

    // TODO
    // 1.      clicked  
    // 2.  button      DB  UPDATE  
    // 3. My page    (  )

    return(
        <main>
            <section className="gj do ir hj sp jr i pg text-black">
                <div className="grid place-items-center static">
                    <div className="text-5xl"> </div>
                    <img src="../../images/seat/seat_map.png" alt=" " className="w-7/12"/>
                    <div className="grid grid-cols-2 z-10 w-28 absolute transform -translate-x-60 -translate-y-28">
                        {seatList &&
                            seatList.map((v, i) => {
                                return (
                                    <div key={i}>
                                        {v.seatNo % 2 === 1 || v.seatNo === 1 ?
                                                <>
                                                {v.seatNo === 7 ?
                                                        <>
                                                            {message && message.seatStatus === 1 ?
                                                                <div className="relative">
                                                                    <span
                                                                        className="absolute mt-3 ms-7 text-white text-sm font-extrabold">{v.seatNo}</span>
                                                                    <img src="../../images/seat/left_none.png"/>
                                                                </div>
                                                                :
                                                                <div className="relative">
                                                                    <span
                                                                        className="absolute mt-3 ms-7 text-red-700 text-sm font-extrabold">{v.seatNo}</span>
                                                                    <img src="../../images/seat/left_clicked.png"/>
                                                                </div>}
                                                        </>
                                                        :
                                                        <>
                                                            {v.status === 'Y' ?
                                                                <>
                                                                    {seatClick ?
                                                                        <button className="relative text-center">
                                                                            <span className="absolute mt-3 text-black text-sm">{v.seatNo}</span>
                                                                            <img src="../../images/seat/left_clicked.png"/>
                                                                        </button>
                                                                        :
                                                                        <button className="relative text-center">
                                                                            <span className="absolute mt-3 text-black text-sm">{v.seatNo}</span>
                                                                            <img src="../../images/seat/left_none.png"/>
                                                                        </button>
                                                                    }
                                                                </>
                                                                :
                                                                <div className="relative text-center">
                                                                    <span
                                                                        className="absolute mt-3 text-black text-sm">{v.seatNo}</span>
                                                                    <img src="../../images/seat/left_used.png"/>
                                                                </div>
                                                            }
                                                        </>
                                                }
                                                </>
                                                :
                                                <>
                                                    {v.status === 'Y' ?
                                                        <>
                                                            {seatClick ?
                                                                <button className="relative text-center">
                                                                    <span className="absolute mt-3 text-sm transform -translate-x-2">{v.seatNo}</span>
                                                                    <img src="../../images/seat/right_clicked.png"/>
                                                                </button>
                                                                :
                                                                <button className="relative text-center">
                                                                    <span className="absolute mt-3 text-black text-sm transform -translate-x-2">{v.seatNo}</span>
                                                                    <img src="../../images/seat/right_none.png"/>
                                                                </button>
                                                            }
                                                        </>
                                                        :
                                                        <div className="relative">
                                                            <span className="absolute mt-3 ms-5 text-black text-sm">{v.seatNo}</span>
                                                            <img src="../../images/seat/right_used.png"/>
                                                        </div>
                                                    }
                                                </>
                                        }
                                    </div>
                                )
                            })}
                    </div>
                </div>
            </section>
        </main>
    )
}